.aside {
  width: 70px;
  height: 100vh;
  box-shadow: 0px 1px 4px 0 rgba(0, 0, 0, 0.1);
  background-color: $charcoal-grey;
  padding: 20px 0;
  position: fixed;
  left: 0px;
  transition: all 600ms;
  top: 0;
  z-index: 10;
  overflow: auto;
  @include r(767) {
    width: 45px; }
  &__content {
    width: 280px;
    @include r(767) {
      width: 240px; } }
  &.active {
    width: 280px;
    @include r(767) {
      width: 240px; }
    .aside__logo {
      padding-left: 130px;
      @include r(767) {
        padding-left: 110px; } }
    .aside__menu__item {
      color: rgba($pale-grey, 0.5);
      img {
        margin-right: 20px; }
      &.active {
        color: $white; } } }
  img {
    width: 30px;
    transition: all 600ms;
    @include r(767) {
      width: 22px; } }
  &__logo {
    padding-bottom: 40px;
    display: flex;
    align-items: center;
    padding-left: 20px;
    transition: all 600ms;
    @include r(767) {
      padding-left: 12px; } }
  &__menu {
    &__item {
      display: flex;
      align-items: center;
      font-size: 12px;
      font-weight: 600;
      color: rgba($pale-grey, 0);
      position: relative;
      &--drop-down {
        &.active {
          &:after {
            transform: translate(-50%) rotate(-90deg); } }
        &:after {
          content: $icon-arrow-left;
          font-family: "iconfont";
          margin-left: 40px;
          position: absolute;
          right: 20%;
          transform: translate(-50%);
          @include tr; } } }
    li {
      position: relative;
      padding: 15px 20px;
      @include tr;
      @include r(767) {
        padding: 15px 10px; }
      &:hover {
        background: rgba(#000, 0.2);
        .aside__menu__item {
          color: $white; }
        i {
          color: $white; } }
      ul {
        border-radius: 4px;
        display: none;
        margin-left: 45px;
        li {
          padding: 5px 0;
          &:hover {
            background: none; } }
        a {
          padding: 6px 20px;
          font-size: 12px;
          font-weight: 600;
          color: rgba($pale-grey, 0.5);
          &:hover {
            color: $pale-grey; } } } }

    a {
      display: flex;
      align-items: center;
      position: relative;
      i {
        position: absolute;
        right: -17px;
        color: $blue-grey;
        font-size: 14px;
        @include tr;
        @include r(767) {
          right: -14px; } } } } }
